@import "@styles/variables";
@import "@styles/mixins";

.button-component {
  padding: 5px 10px;
  border: 2px solid $main-color;
  border-radius: 5px;
  text-transform: uppercase;
  font-size: $standart-font-size;
  @include transition;

  &.button-component_danger {
    border-color: $danger-color;
    path {
      fill: $danger-color;
    }
  }

  &_rounded {
    border-radius: 100%;
    width: 55px;
    height: 55px;
  }

  &__primary {
    background-color: $main-color;
    color: white;
    path {
      fill: white;
    }

    &:hover {
      box-shadow: 0px 0px 0px 5px $main-color-opacity;
    }
    &:active {
      border: 2px solid $main-color-dark;
      background-color: $main-color-dark;
    }

    &.button-component_danger {
      background-color: $danger-color;
      path {
        fill: white;
      }
    }

    &.button-component_danger:hover {
      box-shadow: 0px 0px 0px 5px $danger-color-opacity;
    }
    &.button-component_danger:active {
      border: 2px solid $danger-color-dark;
      background-color: $danger-color-dark;
    }
  }

  &__secondary {
    &:hover {
      box-shadow: 0px 0px 0px 5px $main-color-opacity;
    }
    &:active {
      border: 2px solid $main-color-dark;
      background-color: $main-color-dark;
      color: white;
      path {
        fill: white;
      }
    }

    &.button-component_danger:hover {
      box-shadow: 0px 0px 0px 5px $danger-color-opacity;
    }

    &.button-component_danger:active {
      border: 2px solid $danger-color-dark;
      background-color: $danger-color-dark;
    }
  }
}
